.cards-container{
    padding: 1em;
    height: 60vh;
    width: 70vw;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
.card-container{
    border: 5px solid black;
    border-bottom: 0px;
    box-shadow: 3px 3px 5px 3px rgba(214, 60, 60, 0.623);
}
.card{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}
.card-title{
    color: white;
    font-size: 1.1rem;
    background-color: black;
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.scoreboard{
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;
    justify-content: space-evenly;
}
.displayer{
    font-size: 2rem;
    font-weight: bold;
    font-family: 'Yesteryear', cursive;
    margin-top: 5%;
}