*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../imgs/background.jpg');
    background-size: cover;
    background-position: center;
}
.main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.logo{
    background-image: url('../imgs/logo.jpg');
    background-size: cover;
    width: 307.2px;
    height: 112.8px;
}